<template>
	<view class="news-detail">
		<text class="title">{{ newsList.title }}</text>
		<view class="content boxsiz"><u-parse mode="aspectFill" :imageProp="imageProp" :content="newsList.content" @preview="preview" @navigate="navigate"></u-parse></view>
		<!-- #ifdef MP-WEIXIN -->
		<button class="btn" open-type="share">立即分享</button>
		<!-- #endif -->
		<!-- #ifdef APP-PLUS -->
		<view class="btn" @click="share">立即分享</view>
		<!-- #endif -->
	</view>
</template>

<script>
import { API } from '@/util/api.js';
import { PLATFORM } from '@/util/util.js';
import uParse from '@/components/u-parse/u-parse.vue';

export default {
	data() {
		return {
			newsList: {},
			imageProp: {
				padding: 0,
				lazyLoad: true,
				mode: 'aspectFill'
			}
		};
	},
	onLoad(options) {
		this.id = options.id || options.idd;
	},
	onShow() {
		let loginRes = this.checkLogin('/pages/news/detail', '1', {idd:this.id});
		
		if (loginRes) {
			this.getData();
		}
	},
	onShareAppMessage(res) {
		if (res.from === 'button') {
		}
		return {
			title: this.newsList.title,
			path: '/pages/news/detail?id=' + this.id
		};
	},
	methods: {
		getData() {
			this.request(API.getNewsList, 'GET', { id: this.id },true).then(res => {
				if (res.code === 10000) {
					this.newsList = res.data[0];
				}
			});
		},
		preview(src, e) {
			// do something
		},
		navigate(href, e) {
			// do something
		},
		share() {
			// #ifdef APP-PLUS
			// #endif
			// #ifdef MP-WEIXIN
			// #endif
		}
	},
	components: {
		uParse
	}
};
</script>

<style lang="scss">
@import url('@/components/u-parse/u-parse.css');
@import '../../assets/css/news/detail.scss';
</style>
